}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li, ul {
  list-style: none;
}

button {
  cursor: pointer;
  margin: 0;
  padding: 0;
  display: block;
}

.container {
  margin: 0, auto;
  padding-left: 16px;
  padding-right: 16px;
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1600px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
