  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.37;
}

.card__footer {
  justify-content: space-between;
  display: flex;
}

.card__date {
  text-align: right;
  color: #a8a8a8;
  font-family: Manrope;
  font-size: 16px;
